let play = false;
const app = getApp();
import helper from '../componets/helper.js';
import MescrollMixin from '../componets/mescroll-uni/mescroll-mixins.js';
export default {
	mixins: [MescrollMixin], // 使用mixin
	data() {
		return {
			isShow: true,
			is_like: 0,
			findId: '',
			shadeList: [],
			videoList: {},
			time: 0,
			show_play: false,
			shade: false,
			imgUrl: '',
			totalPage: '',
			page: '1',
			text: '',
			postContent: '',
			userInfoButtonShow: true,
			likeNum: '',
			isShowBox: ''
		};
	},
	onLoad(option) {
		this.imgUrl = this.$helper.imgUrl;
		this.findId = option.id;
		this.getVideo();
		// this.onGetUserInfo();

		uni.getSetting({
			success: res => {
				if (res.authSetting['scope.userInfo']) {
					this.userInfoButtonShow = false;
				}
			}
		});
		uni.request({
			url: `${helper.requestUrl}discover/hide`,
			method: 'GET',
			success: res => {
				res = helper.null2str(res.data);
				if (res.status_code == 'ok') {
					console.log(res.data)
					this.isShowBox = res.data;
				}
			}
		});
	},
	onShareAppMessage(res) {
		uni.request({
			url: `${helper.requestUrl}discover/sharePlus`,
			method: 'POST',
			data: {
				discover_id: this.findId
			},
			success: data => {
				res = helper.null2str(res);
				uni.hideToast();
				if (data.statusCode === 200) {
					this.videoList.share++;
				} else {
				}
			}
		});
		return {
			title: this.videoList.title,
			path: `/pages/findDetail?id=${this.findId}`
		};
	},
	methods: {
		clickVideo() {
			this.videoPlay();
		},
		// 用户信息校验
		verificationUserInfo() {
			if (!this.userInfo.hasOwnProperty('id')) {
				uni.showToast({
					title: '用户信息加载中...',
					icon: 'none'
				});
				return true;
			}
		},
		// 提交用户信息
		onGetUserInfo(e) {
			let that = this;
			if (e.detail.errMsg == 'getUserInfo:ok') {
				var user = e.detail.userInfo
				uni.login({
					success: res => {
						uni.request({
							url: `${helper.requestUrl}auth/login`,
							method: 'POST',
							data: {
								code: res.code,
								nickname: user.nickName,
								sex: user.gender,
								headimgurl: user.avatarUrl,
								mobile: ''
							},
							success: data => {
								res = helper.null2str(res);
								uni.hideToast();
								if (data.statusCode === 200) {
									that.userInfoButtonShow = false;
									let token = data.data.token_type + ' ' + data.data.access_token;
									app.globalData.token = token;
									that.getIndexInfo();
									that.getVideo()
								} else {
									uni.showModal({
										title: '',
										content: data.data.errmsg
									});
								}
							}
						});
					}
				});
			}
		},
		getIndexInfo() {
			// 用户信息获取
			uni.request({
				url: `${helper.requestUrl}auth/me`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					if (res.data.status_code == 'ok') {
						this.userInfo = res.data.data;
						app.globalData.userInfo = res.data.data;
					}
				}
			});
		},
		videoPlay() {
			let video_id = this.videoList.id;
			if (play) {
				this.videoCtx = uni.createVideoContext('myVideo');
				this.videoCtx.play();
				this.show_play = false;
				play = false;
			} else {
				this.videoCtx = uni.createVideoContext('myVideo');
				this.videoCtx.pause();
				this.show_play = true;
				play = true;
			}
		},
		timeupdate(event) {
			let t_w = parseInt(this.width);
			this.duration = event.detail.duration;
			this.time = event.detail.currentTime;
			let width = (this.time / this.duration) * t_w;
			let w = 0;
		},
		// 获取视频
		getVideo() {
			uni.showLoading({
				title: '加载中...'
			});
			uni.request({
				url: `${helper.requestUrl}discover/discoverDetails`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					discover_id: this.findId
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					if (res.data.status_code == 'ok') {
						this.videoList = res.data.data;
						this.is_like = res.data.is_like;
						this.likeNum = this.videoList.like
						if(this.likeNum.toString().length >= 4){
							// let likeN = Math
							let likeN = (this.likeNum/10000).toFixed(1)
							// console.log(likeN)
							this.likeNum = likeN+'W'
						}else{
							this.likeNum = this.videoList.like
						}
					}
				}
			});
		},
		// 评论
		tapShade() {
			this.shadeList = [];
			this.shade = true;
			this.getShade();
		},
		getShade() {
			uni.showLoading({
				title: '加载中...'
			});
			uni.request({
				url: `${helper.requestUrl}discover/commentList`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					discover_id: this.findId,
					page_size: '10',
					page: this.page
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					if (res.data.status_code == 'ok') {
						let curPageData = res.data.data.data;
						this.totalPage = res.data.data.total;
						this.shadeList = this.shadeList.concat(curPageData);
					}
				}
			});
		},
		nextScroll() {
			this.page++;
			this.getShade();
		},
		closeTapShade() {
			this.shade = false;
		},
		post(e) {
			this.postContent = e.detail.value;
		},
		send() {
			if (this.postContent == '') {
				uni.showToast({
					title: '请输入发表内容',
					icon: 'none'
				});
				return;
			}
			uni.showLoading({
				title: '发表中...'
			});
			uni.request({
				url: `${helper.requestUrl}discover/comment`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					discover_id: this.findId,
					content: this.postContent
				},
				success: res => {
					res = helper.null2str(res);
					uni.hideLoading();
					if (res.data.status_code == 'ok') {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
						this.postContent = '';
						this.page = '1';
						this.shadeList = [];
						this.getShade();
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		// 赞
		tapLike(e) {
			let url = '';
			if (e == 0) {
				// 添加关注
				url = 'discover/setLike';
			} else {
				url = 'discover/delLike';
			}
			this.getLike(url);
		},
		getLike(url) {
			uni.request({
				url: `${helper.requestUrl}${url}`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					discover_id: this.findId
				},
				success: res => {
					res = helper.null2str(res.data);
					if (res.status_code == 'ok') {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
						
						
						if (this.is_like == 1) {
							this.is_like = 0;
							this.videoList.like--
							if(this.videoList.like.toString().length >= 4){
								// console.log(this.likeNum --)
								// let likeN = Math
								let likeN = this.videoList.like
								console.log(likeN)
								likeN = (likeN/10000).toFixed(1)
								// console.log(likeN)
								this.likeNum = likeN+'W'
							}else{
								this.likeNum = this.videoList.like
							}
						} else {
							this.videoList.like++
							this.is_like = 1;
							if(this.videoList.like.toString().length >= 4){
								let likeN = this.videoList.like
								console.log(likeN)
								likeN = (likeN/10000).toFixed(1)
								console.log(likeN)
								this.likeNum = likeN+'W'
							}else{
								this.likeNum = this.videoList.like
							}
						}
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
};
